import React from 'react';
import { omit } from 'lodash';
import ProfileKeyboard from 'pages/Profiles/components/profileKeyboard/ProfileKeyboard';
import LoadingSpinner from 'components/LoadingSpinner/LoadingSpinner';
import { Redirect } from 'react-router-dom';
import { paths } from 'routes/constants';
import Confirmation from 'components/ParentalControls/dialogs/Confirmation/Confirmation';
import ParentalControlsInformation from 'components/ParentalControls/dialogs/ParentalControlsInformation/ParentalControlsInformation';
import { CptElements, PageIds } from 'utils/tracking/constants';
import { sendElementClick } from 'utils/tracking/cpt';
import useEditProfile from '../../hooks/useEditProfile';
import { editProfileMachineConfig } from '../editProfileMachine/editProfileMachine';
import { actions, states, editProfileFields } from '../config';
import EditProfileView from '../../EditProfileView';
import WithPinEntryEditProfile from '../../../components/WithPinEntryEditProfile/WithPinEntryEditProfile';

const keyboardConfirm = 'Done';
const keyboardTitle = 'Please give this profile a name';

const EditProfile = () => {
    const {
        currentProfile,
        profileId,
        setDeletedProfile,
        updateProfile,
        setEditedData,
        current,
        getMachineContext,
        clearErrorContext,
        clearEditedData,
        send,
        setCurrentPin,
        currentPin
    } = useEditProfile({ machineConfig: editProfileMachineConfig });

    if (current === states.EDIT_PROFILE) {
        const profileName = getMachineContext().editedData?.nickname || currentProfile?.nickname;
        const contextData = {
            contextValue: getMachineContext(),
            disableError: clearErrorContext
        };
        const isChild = currentProfile?.isChild;

        return (
            <EditProfileView
                context={contextData}
                nickname={profileName}
                currentProfile={currentProfile}
                onDone={() => {
                    if (!isChild) {
                        sendElementClick('adult_profile_edit_done', CptElements.BUTTON, PageIds.PROFILE_SELECT);
                    }

                    send({
                        target: actions.DONE_EDIT_PROFILE,
                        editDataCallback: () => {
                            const parentalControlValue = getMachineContext().editedData?.PARENTAL_CONTROLS_OFF;
                            updateProfile(profileId, {
                                ...omit(getMachineContext()?.editedData, editProfileFields.PIN),
                                ...(parentalControlValue && { isPinProtected: false })
                            });
                        }
                    });
                }}
                onDeleteConfirm={() => {
                    sendElementClick('kids_profile_delete_2', CptElements.BUTTON, PageIds.PROFILE_SELECT);
                    setDeletedProfile(currentProfile.profileId);
                    send({ target: actions.DELETE_PROFILE });
                }}
                editProfileName={() => {
                    if (!isChild) {
                        sendElementClick('adult_profile_edit_name', CptElements.BUTTON, PageIds.EDIT_ADULT_PROFILE_NAME);
                    }
                    send({ target: actions.EDIT_PROFILE_NAME });
                }}
                setEditProfileField={(data) => {
                    setEditedData(data);
                    const isPinProtected = getMachineContext().editedData?.[editProfileFields.IS_PIN_PROTECTED];
                    if (isPinProtected && data?.value) {
                        send({ target: actions.SET_PARENTAL_CONTROLS });
                    }
                    if (!isPinProtected && !data?.value) {
                        send({ target: actions.TURN_PARENTAL_CONTROLS_OFF });
                    }
                }}
            />
        );
    }

    if (current === states.TURN_OFF_PC) {
        return (
            <ParentalControlsInformation
                onConfirm={() => send({ target: actions.CONFIRM_TURN_PC_OFF })}
                onCancel={() => {
                    clearEditedData();
                    send({ target: actions.CANCEL_TURN_PC_OFF });
                }}
            />
        );
    }

    if (current === states.PROFILE_KEYBOARD) {
        const proceedProfileName = (newProfileName) => {
            setEditedData({ type: editProfileFields.NICKNAME, value: newProfileName });

            return send({ target: actions.DONE_PROFILE_NAME });
        };

        return (
            <ProfileKeyboard
                title={keyboardTitle}
                onConfirm={(entry) => proceedProfileName(entry)}
                onCancel={() => send({ target: actions.CANCEL_PROFILE_NAME })}
                confirmLabel={keyboardConfirm}
            />
        );
    }

    if (current === states.CREATE_PIN) {
        return (
            <WithPinEntryEditProfile
                onCancel={() => {
                    clearEditedData(editProfileFields.IS_PIN_PROTECTED);
                    send({ target: actions.CANCEL_SET_PIN });
                }}
                onConfirm={() => {
                    send({ target: actions.CONFIRM_SET_PIN });
                }}
                onPINEntered={(value) => setCurrentPin(value)}
            />
        );
    }

    if (current === states.CONFIRMATION) {
        return (
            <Confirmation
                onConfirm={() => {
                    setEditedData({ type: editProfileFields.PIN, value: currentPin });
                    send({ target: actions.CONFIRM_CONFIRMATION });
                    clearEditedData(editProfileFields.PARENTAL_CONTROLS_OFF);
                }}
                onCancel={() => {
                    clearEditedData(editProfileFields.IS_PIN_PROTECTED);
                    send({ target: actions.CANCEL_CONFIRMATION });
                }}
                turnOnParentalControls={false}
                isEditProfile
            />
        );
    }

    if (current === states.LOADING || current === states.LOADING_DELETE) {
        return <LoadingSpinner />;
    }

    if (current === states.REDIRECT) {
        return <Redirect push to={paths.profiles} />;
    }

    return <Redirect push to={paths.profiles} />;
};

export default EditProfile;
