import React from 'react';
import cn from 'classnames';
import Heading from '@itv/fe-components/Heading';
import { editProfileFields, focusIds } from 'pages/Profiles/EditProfile/StateManager/config';
import FocusSwitch from 'components/FocusSwitch/FocusSwitch';
import FocusGroup from 'focus/FocusGroup';
import PropTypes from 'prop-types';
import './ParentalControls.scss';

const className = 'parental-controls';

const ParentalControls = ({ setEditProfileField, isParentalControlsEnabled, renderButton, onBack }) => {
    const areParentalControlsEnabled = isParentalControlsEnabled();

    return (
        <FocusGroup id={focusIds.EDIT_PROFILE_PARENTAL_CONTROLS_SWITCH}>
            <div className={cn(className)}>
                <FocusGroup orientation="column" className={`${className}__settingsWrapper`}>
                    <div className={cn(`${className}__headerContainer`)}>
                        <FocusSwitch
                            id="focusSwitchInput"
                            backTo={onBack}
                            label="Parental Controls"
                            defaultToggle={areParentalControlsEnabled}
                            onClick={(value) => {
                                if (!value) {
                                    setEditProfileField({ type: editProfileFields.IS_PIN_PROTECTED, value: null });
                                    setEditProfileField({ type: editProfileFields.PIN, value: null });
                                    setEditProfileField({
                                        type: editProfileFields.PARENTAL_CONTROLS_OFF,
                                        value: true
                                    });
                                }
                                setEditProfileField({ type: editProfileFields.IS_PIN_PROTECTED, value });
                            }}
                        />
                    </div>
                    {renderButton()}
                </FocusGroup>
                <div className={cn(`${className}__info-container`)}>
                    <div className={`${className}__info`}>
                        <Heading className={`${className}__info-heading`} element="h3">
                            How do Parental Controls work?
                        </Heading>
                        <p className={`${className}__info-statement`}>
                            When you turn on Parental Controls, you&apos;ll be asked to enter your PIN every time you want to access your
                            profile or watch restricted content.
                        </p>
                    </div>
                </div>
            </div>
        </FocusGroup>
    );
};

ParentalControls.propTypes = {
    setEditProfileField: PropTypes.func.isRequired,
    renderButton: PropTypes.func.isRequired,
    onBack: PropTypes.func.isRequired,
    isParentalControlsEnabled: PropTypes.func.isRequired
};

export default ParentalControls;
