import { useCallback, useEffect, useState } from 'react';
import useUserOptions from 'hooks/useUserOptions';
import { useParams } from 'react-router-dom';
import { useProfiles } from 'contexts/ProfilesContext';
import useMachine from 'hooks/useMachine/useMachine';

const useEditProfile = ({ machineConfig }) => {
    const { profiles, setDeletedProfile, updateProfile } = useProfiles();
    const { userId, accessToken } = useUserOptions();
    const { profileId } = useParams();
    const {
        machineState: { current },
        send,
        setMachineContext,
        getMachineContext,
        clearErrorContext
    } = useMachine(machineConfig, 'editedData');
    const [currentPin, setCurrentPin] = useState(null);

    const currentProfile = profiles.find((profile) => profile.profileId === profileId);

    const setEditedData = useCallback(
        (field) => {
            const currentData = getMachineContext().editedData;
            setMachineContext({
                editedData: {
                    ...currentData,
                    [field.type]: field.value
                }
            });
        },
        [getMachineContext, setMachineContext]
    );

    const clearEditedData = useCallback(
        (fieldName) => {
            if (!fieldName)
                return setMachineContext({
                    editedData: {}
                });
            const { [fieldName]: currentField, ...currentData } = getMachineContext().editedData;
            return setMachineContext({
                editedData: {
                    ...currentData
                }
            });
        },
        [getMachineContext, setMachineContext]
    );

    useEffect(() => {
        setMachineContext({
            userId,
            accessToken,
            profileId,
            currentProfile
        });
    }, [accessToken, currentProfile, profileId, setMachineContext, userId]);

    return {
        currentProfile,
        profileId,
        setDeletedProfile,
        updateProfile,
        setEditedData,
        current,
        getMachineContext,
        clearErrorContext,
        clearEditedData,
        send,
        setCurrentPin,
        currentPin
    };
};

export default useEditProfile;
