import React, { useState, useEffect } from 'react';
import { has } from 'lodash';
import cn from 'classnames';
import PropTypes from 'prop-types';
import Header from 'pages/Profiles/EditProfile/components/header/Header';
import ParentalControls from 'pages/Profiles/EditProfile/components/parentalControls/ParentalControls';
import FocusGroup from 'focus/FocusGroup';
import Button from 'components/Button';
import { useHistory } from 'react-router-dom';
import KidsErrorModal from 'pages/Profiles/components/KidsErrorModal/KidsErrorModal';
import { paths } from 'routes/constants';
import { CptEvents, PageTypes } from 'utils/tracking/constants';
import { cptSend } from 'utils/tracking/cpt';
import { contextPropTypes, editProfileFields, focusIds } from './StateManager/config';
import { useParentalControls } from '../../../contexts/ParentalControlsContext';
import './EditProfileView.scss';

const className = 'EditProfile';

const EditProfileView = ({ currentProfile, nickname, editProfileName, onDone, setEditProfileField, onDeleteConfirm, context }) => {
    const [showError, setShowError] = useState(false);
    const history = useHistory();
    const { areParentalControlsOn } = useParentalControls();

    const { contextValue, disableError } = context;
    const isChild = currentProfile?.isChild;

    const parentalControlsState = () => {
        if (has(contextValue.editedData, editProfileFields.PARENTAL_CONTROLS_OFF)) {
            return !contextValue.editedData.PARENTAL_CONTROLS_OFF;
        }
        return areParentalControlsOn() || contextValue.editedData?.isPinProtected;
    };

    useEffect(() => {
        if (!isChild) {
            cptSend({
                event: CptEvents.SCREEN_LOAD_EVENT,
                screen: {
                    name: 'ctv.account.adult-profile-edit',
                    type: PageTypes.ACCOUNT
                }
            });
        }
    }, [isChild]);

    useEffect(() => {
        const errorState = has(contextValue?.errors, 'editedData');
        setShowError(errorState);
    }, [contextValue]);

    const onBack = () => {
        history.push({
            pathname: paths.profiles,
            state: { isEditMode: true, editProfileId: currentProfile?.profileId }
        });
    };

    const doneButton = (additionalClass = '') => (
        <Button id="done-button" className={cn(`${className}__done-button`, additionalClass)} onClick={onDone} backTo={onBack}>
            Done
        </Button>
    );

    const defaultFocus = () => {
        if (!isChild) {
            return contextValue?.currentFocus || focusIds.EDIT_PROFILE_PARENTAL_CONTROLS_SWITCH;
        }
        return focusIds.EDIT_PROFILE_HEADER;
    };

    return (
        <div id="EditProfilePage" className={className}>
            <FocusGroup id="edit-profile" orientation="column" initialFocus={defaultFocus()}>
                <Header
                    id="header"
                    isChild={isChild}
                    nickname={nickname}
                    onDeleteConfirm={onDeleteConfirm}
                    onBack={onBack}
                    showProfileKeyboard={editProfileName}
                />
                {!isChild && (
                    <ParentalControls
                        id="parental-controls"
                        onBack={onBack}
                        setEditProfileField={setEditProfileField}
                        isParentalControlsEnabled={parentalControlsState}
                        renderButton={doneButton}
                    />
                )}
                {isChild && doneButton(`${className}__done-button--child`)}
            </FocusGroup>
            {showError && (
                <KidsErrorModal
                    onConfirm={() => {
                        setShowError(false);
                        disableError();
                    }}
                />
            )}
        </div>
    );
};

EditProfileView.defaultProps = {
    context: { contextValue: {} }
};

EditProfileView.propTypes = {
    currentProfile: PropTypes.shape({
        isChild: PropTypes.bool,
        profileId: PropTypes.string
    }).isRequired,
    nickname: PropTypes.string.isRequired,
    editProfileName: PropTypes.func.isRequired,
    onDone: PropTypes.func.isRequired,
    setEditProfileField: PropTypes.func.isRequired,
    onDeleteConfirm: PropTypes.func.isRequired,
    context: contextPropTypes
};

export default EditProfileView;
