import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import Button from 'components/Button';
import OverlayModal from 'components/OverlayModal';
import FocusGroup from 'focus/FocusGroup';
import Heading from '@itv/fe-components/Heading';
import './DeleteProfileModal.scss';

import { CptEvents, PageTypes } from 'utils/tracking/constants';
import { cptSend } from 'utils/tracking/cpt';

const DeleteProfileModal = ({ showModal, onDeleteConfirm }) => {
    const className = 'DeleteProfileModal';

    useEffect(() => {
        cptSend({
            event: CptEvents.SCREEN_LOAD_EVENT,
            screen: {
                name: 'ctv.account.kids-profile-delete',
                type: PageTypes.ACCOUNT
            }
        });
    }, []);

    return (
        <OverlayModal className={className}>
            <Heading className={`${className}__heading`}>Delete kids profile?</Heading>
            <span className={`${className}__prompt`}>Are you sure? All of this profile&apos;s history will be deleted.</span>
            <FocusGroup id="deleteProfileModal" greedy>
                <Button
                    id="deleteProfileModalCancelButton"
                    className={`${className}__cancel-button`}
                    onClick={() => showModal(false)}
                    backTo={() => showModal(false)}
                >
                    Cancel
                </Button>
                <Button id="deleteProfileModalDeleteButton" onClick={onDeleteConfirm}>
                    Delete profile
                </Button>
            </FocusGroup>
        </OverlayModal>
    );
};

DeleteProfileModal.propTypes = {
    showModal: PropTypes.func.isRequired,
    onDeleteConfirm: PropTypes.func.isRequired
};

export default DeleteProfileModal;
