import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Button from 'components/Button';
import { focusIds } from 'pages/Profiles/EditProfile/StateManager/config';
import FocusGroup from 'focus/FocusGroup';
import HorizontalRule from '@itv/fe-components/HorizontalRule';
import { ProfileAvatar, AvatarSize, ProfileType, OrientationTypeProp } from '@itv/fe-components/ProfileAvatar';
import './Header.scss';

import { CptElements, PageIds } from 'utils/tracking/constants';
import { sendElementClick } from 'utils/tracking/cpt';
import { useFocusState } from 'focus/FocusProvider';
import DeleteProfileModal from '../deleteProfileModal';

const Header = ({ isChild, nickname, showProfileKeyboard, onDeleteConfirm, onBack }) => {
    const { currentFocus } = useFocusState();
    const [showDeleteProfileModal, setShowDeleteProfileModal] = useState(false);
    const className = 'header';

    const handleDeleteProfileClick = () => {
        sendElementClick('kids_profile_delete_1', CptElements.BUTTON, PageIds.KIDS_PROFILE_DELETE);
        setShowDeleteProfileModal(true);
    };

    return (
        <header className={className}>
            <FocusGroup id={focusIds.EDIT_PROFILE_HEADER} orientation="row" jumpToLastFocus>
                <div className={`${className}__elements`}>
                    <div className={`${className}__avatarContainer`}>
                        <Button id="ProfileAvatar__button" className="ProfileAvatar__button" onClick={showProfileKeyboard} backTo={onBack}>
                            <ProfileAvatar
                                type={isChild ? ProfileType.kids : ProfileType.user}
                                size={AvatarSize.large}
                                isActive={currentFocus?.id === 'ProfileAvatar__button'}
                                labelText={nickname}
                                orientation={OrientationTypeProp.row}
                            />
                        </Button>
                    </div>
                    {isChild && (
                        <div className={`${className}__delete-profile`}>
                            <Button className={`${className}__delete-button`} onClick={handleDeleteProfileClick} backTo={onBack}>
                                Delete profile
                            </Button>
                        </div>
                    )}
                </div>
            </FocusGroup>
            <HorizontalRule className={`${className}__rule`} />
            {showDeleteProfileModal && (
                <DeleteProfileModal onDeleteConfirm={onDeleteConfirm} showModal={(showModal) => setShowDeleteProfileModal(showModal)} />
            )}
        </header>
    );
};

Header.propTypes = {
    isChild: PropTypes.bool.isRequired,
    nickname: PropTypes.string.isRequired,
    onDeleteConfirm: PropTypes.func.isRequired,
    onBack: PropTypes.func.isRequired,
    showProfileKeyboard: PropTypes.func.isRequired
};

export default Header;
