import { user } from '@itv/fe-cerberus';
import { isEmpty, has, omit } from 'lodash';
import { cptSend } from 'utils/tracking/cpt';
import { CptEvents, PageIds, PageTypes } from 'utils/tracking/constants';
import { editProfileFields, states, focusIds } from '../config';

// eslint-disable-next-line import/prefer-default-export
export const editProfileMachineConfig = {
    initial: states.EDIT_PROFILE,
    states: {
        editProfile: {
            on: {
                DONE_EDIT_PROFILE: states.LOADING,
                EDIT_PROFILE_NAME: states.PROFILE_KEYBOARD,
                SET_PARENTAL_CONTROLS: states.CREATE_PIN,
                TURN_PARENTAL_CONTROLS_OFF: states.TURN_OFF_PC,
                DELETE_PROFILE: states.LOADING_DELETE
            }
        },
        turnOffPC: {
            on: {
                CONFIRM_TURN_PC_OFF: states.EDIT_PROFILE,
                CANCEL_TURN_PC_OFF: states.EDIT_PROFILE
            },
            effect: ({ setMachineContext }) => {
                setMachineContext({ currentFocus: focusIds.EDIT_PROFILE_PARENTAL_CONTROLS_SWITCH });
            }
        },
        profileKeyboard: {
            on: {
                CANCEL_PROFILE_NAME: states.EDIT_PROFILE,
                DONE_PROFILE_NAME: states.EDIT_PROFILE
            },
            effect: ({ setMachineContext }) => {
                setMachineContext({ currentFocus: focusIds.EDIT_PROFILE_HEADER });
            }
        },
        createPin: {
            on: {
                CANCEL_SET_PIN: states.EDIT_PROFILE,
                CONFIRM_SET_PIN: states.CONFIRMATION
            },
            effect: ({ setMachineContext }) => {
                setMachineContext({ currentFocus: focusIds.EDIT_PROFILE_PARENTAL_CONTROLS_SWITCH });
            }
        },
        confirmation: {
            on: {
                CANCEL_CONFIRMATION: states.EDIT_PROFILE,
                CONFIRM_CONFIRMATION: states.EDIT_PROFILE
            },
            effect: ({ setMachineContext }) => {
                setMachineContext({ currentFocus: focusIds.EDIT_PROFILE_PARENTAL_CONTROLS_SWITCH });
            }
        },
        loading: {
            on: {
                ERROR: states.EDIT_PROFILE,
                SUCCESS: states.REDIRECT
            },
            effect: async ({ invokeNextStep, getMachineContext, setMachineErrorContext, props }) => {
                const { userId, accessToken, profileId, editedData } = getMachineContext();
                if (isEmpty(editedData)) {
                    invokeNextStep({ target: 'SUCCESS' });
                    return;
                }

                if (has(editedData, 'pin')) {
                    const cptDetails = editedData.pin ? PageIds.ADULT_PROFILE_ENABLE_PIN : PageIds.ADULT_PROFILE_DISABLE_PIN;
                    cptSend({
                        event: CptEvents.FORM_SUBMIT_EVENT,
                        form: {
                            id: cptDetails,
                            name: cptDetails,
                            category: PageTypes.ACCOUNT
                        }
                    });
                }

                try {
                    await user.accountProfiles.updateProfile({
                        userId,
                        profileId,
                        data: omit(editedData, [editProfileFields.IS_PIN_PROTECTED, editProfileFields.PARENTAL_CONTROLS_OFF]),
                        auth: accessToken
                    });
                    props.editDataCallback();
                    invokeNextStep({ target: 'SUCCESS' });
                } catch (e) {
                    setMachineErrorContext({ editedData: true });
                    invokeNextStep({ target: 'ERROR' });
                }
            }
        },
        loadingDelete: {
            on: {
                ERROR: states.EDIT_PROFILE,
                SUCCESS: states.REDIRECT
            },
            effect: async ({ invokeNextStep, getMachineContext }) => {
                const { userId, accessToken, profileId } = getMachineContext();

                try {
                    await user.accountProfiles.deleteProfile({
                        userId,
                        profileId,
                        auth: accessToken
                    });
                    invokeNextStep({ target: 'SUCCESS' });
                } catch (e) {
                    invokeNextStep({ target: 'ERROR' });
                }
            }
        },
        REDIRECT: {
            on: {},
            final: true
        }
    }
};
