/* eslint-disable react/jsx-props-no-spreading */
import React, { useState } from 'react';
import PINEntry from 'components/PINEntry/PINEntry';
import PropTypes from 'prop-types';
import { useParentalControls } from 'contexts/ParentalControlsContext';

const WithPinEntryEditProfile = (props) => {
    const [pinValue, setPinValue] = useState('');
    const {
        actions: { setUserPin }
    } = useParentalControls();

    const { onConfirm, onCancel, onPINEntered, ...restProps } = props;

    const pinEnteredCallback = (value) => {
        setPinValue(value);
        onPINEntered(value);
    };

    return (
        <>
            <PINEntry
                onConfirm={() => {
                    setUserPin(pinValue);
                    onConfirm();
                }}
                onCancel={onCancel}
                onPINEntered={(value) => pinEnteredCallback(value)}
                {...restProps}
            />
        </>
    );
};

WithPinEntryEditProfile.propTypes = {
    onPINEntered: PropTypes.func.isRequired,
    onConfirm: PropTypes.func.isRequired,
    onCancel: PropTypes.func.isRequired
};

export default WithPinEntryEditProfile;
