import React, { useState } from 'react';
import PropTypes from 'prop-types';
import useFocus from 'focus/useFocus';
import Switch from '@itv/fe-components/Switch';
import SharedPropTypes from '../../helpers/propTypes';

const FocusSwitch = ({ id, backTo, label, position, throttle, insertAfterId, insertBeforeId, onClick, defaultToggle }) => {
    const { focusId, ref } = useFocus(id, {
        backTo,
        position,
        throttle,
        insertAfterId,
        insertBeforeId
    });
    const [focused, setFocused] = useState(false);

    const onFocus = () => {
        setFocused(true);
    };

    const onBlur = () => {
        setFocused(false);
    };
    return (
        <Switch
            id="focusSwitch"
            focusId={focusId}
            ref={ref}
            label={label}
            focused={focused}
            onFocus={onFocus}
            onBlur={onBlur}
            defaultToggle={defaultToggle}
            onClick={(isChecked) => onClick(!isChecked)}
        />
    );
};

FocusSwitch.defaultProps = {
    id: '',
    label: '',
    insertBeforeId: null,
    insertAfterId: null,
    backTo: null,
    position: null,
    defaultToggle: false,
    onClick: () => {},
    throttle: {
        horizontal: 0,
        vertical: 0
    }
};

FocusSwitch.propTypes = {
    id: PropTypes.string,
    label: PropTypes.string,
    insertBeforeId: PropTypes.string,
    insertAfterId: PropTypes.string,
    backTo: SharedPropTypes.backTo,
    defaultToggle: PropTypes.bool,
    onClick: PropTypes.func,
    position: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.number), PropTypes.number]),
    throttle: PropTypes.shape({
        horizontal: PropTypes.number,
        vertical: PropTypes.number
    })
};

export default FocusSwitch;
